<template>
  <div class="usersApply h100">
    <!-- <publicCrumbs :crumbsData="crumbsData"></publicCrumbs> -->
    <div class="page-main-box">
      <KlTable
        v-bind="tableConfig"
        v-if="pageType == 'list'"
        ref="klTable"
      ></KlTable>
      <editPage
        v-if="pageType !== 'list'"
        @goBackPage="goBackPage"
        :rightBtnArr="rightBtnArr"
      >
        <addInfo
          ref="editForm"
          :pageType.sync="pageType"
          :formData="formData"
        ></addInfo>
      </editPage>
    </div>
    <dialogPublic
      :title="'问题管理-分配'"
      :show.sync="distributionDialog"
      @doSubmit="distributionSubmit"
      @doClose="() => (distributionDialog = false)"
      appendTobody
      width="800px"
      height="400px"
    >
      <distributionDialog
        :distributionData="distributionData"
        ref="distributionFrom"
      >
      </distributionDialog>
    </dialogPublic>
  </div>
</template>

<script>
import addInfo from "./add";
import {
  getListApi, //获取列表
  deleteApi, //删除
  addApi, //添加
  updateApi //编辑
} from "@/api/itOperations/problemManagement";
import {allocationEmp} from "@/api/workFlow";
import distributionDialog from "./distributionDialog";
export default {
  name: "problemManagement",
  components: { addInfo, distributionDialog },
  data() {
    return {
      label: "",
      otherInfo: "",
      showScore: false,
      distributionDialog:false,
      distributionData:{},
      currentItem: {},
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "daterange",
              label: "申请日期",
              model: "billDate"
            },
            {
              type: "el-input",
              label: "服务单号",
              model: "orderNo",
              placeholder: "服务单号"
            },
            {
              type: "el-input",
              label: "申请人",
              model: "staffunName",
              placeholder: "申请人"
            },
            {
              type: "el-select",
              label: "审核状态",
              model: "status",
              placeholder: "审核状态",
              option: this.$store.getters["dirtData"]("flow_type")
            },
          ]
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            page: 1,
            pageSize: 15 //pageCount
          }
        },
        tableDataObj: {
          tableArr: [],
          total: null
        },
        operateArr: [
          {
            name: "新建",
            // isHide: !this.$store.getters["isBtnAuth"]("insert"),
            icon: "el-icon-plus",
            method: name => {
              this.toAdd();
            }
          }
          // {
          //   name: "导出",
          //   icon: "el-icon-download",
          //   // isHide: !this.$store.getters["isBtnAuth"]("export"),
          //   method: name => {
          //     this.toExport();
          //   }
          // }
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: val => {
              this.selectionData = val;
            }
          },
          // table表展示列配置
          showItems: [
            {
              name: "服务单号",
              prop: "orderNo",
              width: 100
            },
            {
              name: "申请日期",
              prop: "billDate"
            },

            {
              name: "申请人",
              prop: "staffunName"
            },
            {
              name: "级别类型", //level_type
              prop: "levelType",
              scopeType: "formate",
              method: row => {
                let value = row.levelType;
                return (
                  this.$store.getters["fineDirtDataName"](
                    "level_type",
                    value
                  ) || value
                );
              }
            },
            {
              name: "问题分类", //pro_type
              prop: "proType",
              scopeType: "formate",
              method: row => {
                let value = row.proType;
                return (
                  this.$store.getters["fineDirtDataName"]("pro_type", value) ||
                  value
                );
              }
            },
            {
              name: "描述",
              prop: "appContent"
            },
            {
              name: "责任人",
              prop: "assignName",
              id: "assignEmpid"
            },
            {
              name: "备注",
              prop: "remark"
            },
            {
              name: "审批人",
              prop: "auditorName"
            },
            {
              name: "流程节点",
              prop: "stage"
            },
            {
              name: "流程状态",
              prop: "flowStatusName",
              id: "status"
            },
            {
              name: "操作",
              prop: "operate",
              width: 150,
              scopeType: "button",
              fixed: "right",
              buttonArr: [
                // 最后一行操作栏数组
                {
                  name: "详情",
                  method: row => {
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                  },
                  icon: "false"
                },
                {
                  name: "审批",
                  method: row => {
                    //0不可以审核 1可审核
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.workflowIsApprover;
                  },
                  icon: "false"
                },
                {
                  name: "编辑",
                  method: row => {
                    this.editInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.status === 2;
                  },
                  icon: "false"
                },
                {
                  name: "评分",
                  method: row => {
                    this.score(row);
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 1;
                  },
                  icon: "false"
                },
                {
                  name: "分配",
                  method: row => {
                    // console.log(row);
                    this.distributionData = row;
                    this.distributionDialog = true;
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 1;
                  },
                  icon: "false"
                },
                {
                  name: "删除",
                  method: row => {
                    this.deleteInfo(row);
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 2;
                  },
                  icon: "false"
                }
              ]
            }
          ]
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100]
        },
        paginationIsshow: true,
        showklTable: false
      },
      pageType: "list",
      formData: {}
    };
  },
  computed: {
    rightBtnArr() {
      let arr = [
        {
          text: "保存",
          icon: "el-icon-check",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(2);
          }
        },
        {
          text: "提交",
          icon: "el-icon-finished",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(1);
          }
        }
      ];
      return arr;
    }
  },
  watch: {
    pageType(val) {
      if (val === "list") {
        this.getTableData();
      }
    }
  },

  mounted() {
    this.getTableData();
    // console.log(this.$store.getters["fineDirtDataName"]("level type",2) );
  },
  methods: {
    //新建
    toAdd() {
      this.pageType = "add";
      this.formData = {};
    },
    //查询数据
    getTableData() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      form.pageCount = form.pageSize;
      //日期处理
      let { billDate } = form;
      if (billDate) {
        let [beginDate, endDate] = billDate;
        form.beginDate = beginDate;
        form.endDate = endDate;
      }
      this.$delete(form, "billDate");
      this.$delete(form, "pageSize");
      this.$delete(form, "empId");
      getListApi(form)
        .then(res => {
          // console.log(res.data.list);
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch(res => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },

    //分配
    addDistribution(data) {
      return new Promise((resolve, reject) => {
        addPortion(data)
          .then(res => {
            resolve(res);
          })
          .catch(e => {
            console.log(e);
            reject(e);
          });
      });
    },
    //详情
    detailsInfo(row) {
      this.pageType = "details";
      this.formData = Object.assign({}, row);
    },
    //编辑
    editInfo(row) {
      this.pageType = "edit";
      this.formData = Object.assign({}, row);
    },
    //评分
    score(row) {
      this.pageType = "score";
      this.formData = Object.assign({}, row);
    },
     //分配弹框确定按钮
    distributionSubmit(){
      this.$refs.distributionFrom
        .getFormData()
        .then(data => {
          let {assignEmpid,procInstType,id } = data;
          let form={
            assignEmpid:assignEmpid,
            procInstType:procInstType,
            id:id
          }
          // console.log(form);
          this.addDistribution(form).then(res => {
            this.$message.success("操作成功！");
            this.distributionDialog = false
            // this.pageType = "list";
            this.getTableData();
            // this.pageType = "edit";
            // this.$refs.editForm.getDetails(this.formData.id);
          });

        })
        .catch(e => {
          console.log(e);
        });
    },
    addDistribution(data){
       return new Promise((resolve, reject) => {
          allocationEmp(data)
              .then((res) => {
                resolve(res);
              })
              .catch((e) => {
                console.log(e);
                reject(e);
              });
        });

    },

    //删除
    deleteInfo(row) {
      this.$confirm(`是否确认删除`, "提示", {
        // this.$confirm(`是否删除【${row.trainingName}】`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteApi(row.id).then(res => {
            this.$message.success("操作成功");
            this.getTableData();
          });
        })
        .catch(e => {});
    },
    //点击提交
    doSubmit(submitType) {
      this.$refs.editForm
        .getFormData()
        .then(data => {
          // console.log(data);
          this.$set(data, "submitType", submitType);
          this.$delete(data, "assetsNumber");
          this.$delete(data, "appsysName");
          const actionApi = queryData => {
            //addApi
            return queryData.id ? updateApi(queryData) : addApi(queryData);
          };
          actionApi(data).then(res => {
            this.$message.success("操作成功！");
            if (submitType == 2) {
              this.pageType = "edit";
              if (res.data) {
                this.formData.id = res.data;
              }
              this.$refs.editForm.getDetails(this.formData.id);
            } else {
              this.pageType = "list";
            }
          });
        })
        .catch(e => {
          console.log(e);
        });
    },
    //返回
    goBackPage(data) {

      this.pageType = "list";

    },
    /**导出 */
    toExport() {
      let baseUrl = process.env.BASEURL;
      let params = "";
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      if (form.daterange) {
        form.comeinStart = form.daterange[0] || "";
        form.comeinEnd = form.daterange[1] || "";
        delete form.daterange;
      }
      for (const key in form) {
        params += `${key}=${form[key]}&`;
      }

      params = params.substring(0, params.length - 1);
      let url = `${baseUrl}/trainRecord/exportAdmUnPass.htm?${params}`;
      window.open(url);
    }
  }
};
</script>

