<template>
  <div class="distributionDialog">
    <editForm :formConfig="formConfig" ref="editForm" :formData="editFormData">
    </editForm>
  </div>
</template>

<script>
export default {
  props: {
    distributionData: {
      type: Object,
      default: {}
    },
    maxNum: {
      type: Number,
      default: 0
    }
  },
  name: "distributionDialog",
  watch: {
    distributionData: {
      handler: function(newV,oldV) {
        this.editFormData = Object.assign({}, newV);
      },
      deep: true
    }
  },
  data() {
    return {
      editFormData: {},
      formConfig: {
        itemArr: [
          {
            label: "申请日期",
            prop: "billDate",
            type: "date",
            disabled: true,
            required: true,
            span: 24
          },
          {
            label: "申请人",
            prop: "staffunName",
            required: true,
            disabled: true,
            type: "text",
            span: 24
          },
          {
            type: "select",
            label: "级别类型",
            prop: "levelType",
            required: true,
            disabled: true,
            seleteOption: this.$store.getters["dirtData"]("level_type"),
            formatter: val => {
              return this.$store.getters["fineDirtDataName"]("level_type", val);
            },
            span: 24
          },
          {
            type: "select",
            label: "问题分类",
            prop: "proType",
            required: true,
            disabled: true,
            seleteOption: this.$store.getters["dirtData"]("pro_type"),
            formatter: val => {
              return this.$store.getters["fineDirtDataName"]("pro_type", val);
            },
            span: 24
          },
          {
            label: "责任人",
            prop: "assignName",
            idsStr: "assignEmpid",
            type: "user",
            trigger: "change",
            required: true,
            span: 24
          }
        ]
      }
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      // console.log("hello");
      this.editFormData = Object.assign({}, this.distributionData);
    },
    //获取表单数据
    getFormData() {
      let This = this;
      return new Promise((resolve, reject) => {
        this.$refs.editForm
          .getFormData()
          .then(data => {
            data = Object.assign({}, data);
            resolve(data);
          })
          .catch(e => {
            reject(e);
          });
      });
    }
  }
};
</script>

